* {
  padding: 0;
  margin: 0;
}
.body {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.ctr {
  width: 40%;
  height: 80vh;
}
.ctr-img {
  width: 100%;
  height: 40vh;
  background-image: url('../Utils/krishna.webp');
  background-repeat: no-repeat;
  background-size: contain;
}
.p-tatva {
  width: 100%;
  font: 20px Arial red;
}
.counter {
  display: flex;
  justify-content: center;
}
input {
  height: 5vh;
  padding: 1px;
}
button {
  background-color: #f4c430;
  color: #fff;
  border: none;
  width: 10%;
  font-weight: 900;
}
